body {
	font-family: 'Heebo', sans-serif;
	font-weight: 400;
	font-size: 1.4em;
	color: #404040;
	max-width: 46.125rem;
	margin: 1.1em 1.2em;
	-webkit-font-smoothing: subpixel-antialiased !important;
}

a {
	color: #404040;
}

a:visited {
	color: #404040;
}

a:hover,
a:focus,
a:active {
	text-decoration: underline;
}

a:focus {
	outline: thin dotted;
}

a:hover,
a:active {
	outline: 0;
}

p {
	margin-bottom: 1.5em;

	&:first-child {
		margin-top: 0;
	}
}